@import url('https://fonts.googleapis.com/css2?family=Poppins&family=Ubuntu:wght@300&display=swap');
.container{
    margin: auto;
    height: 50vh;
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.h-primary{
    margin-top: 100px;
    font-family: 'Poppins', sans-serif;
}
.border{
    border: 2px solid black;
}
.calc{
    display: flex;
    flex-direction: row;
}
.box1{
    display: grid;
    grid-template-columns: auto auto auto;
}
.form-control{
    width: 96%;
    border: none;
    background-color: white;
    font-family: 'Ubuntu', sans-serif;
    font-size: 18px;
    resize: none;
    padding: 2px 4px;
}
.form-control:focus{
    outline: none;
    border: none;
    cursor: default;
}
.result{
    height: 22px;
    font-size: 20px;
    font-weight: 500;
    padding: 0 0 5px 5px;
}
.space{
    display: flex;
}
.clear{
    width: 50%;
    border: 1px solid black;
}
.clear button.btn{
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 32px;
    margin: auto;
    /* padding: 10px 0px 10px 30px; */
}