*{
    margin: 0;
    padding: 0;
    /* box-sizing: border-box; */
}
.item{
    display:block;
    height: 50px;
    width: 50px;
    border: 1px solid black;
    text-align: center;
}
.btn{
    border: none;
    background-color: white;
    /* margin-top: 20px; */
    padding: 15px 20px;
}
.btn:hover{
    cursor: pointer;
    text-align: center;
}